body {
  margin: 0;
}

:root {
  --main-color: rgba(234, 0, 94, 1);
  --secondary-color: coral;
}

.app-container {
  font-family: 'Play', sans-serif;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  max-width: 900px;
  min-width: 380px;
  min-height: 700px;
  background-image: linear-gradient(
    to bottom right,
    var(--main-color) 55%,
    var(--secondary-color)
  );
  border-radius: 4px;
  padding-bottom: 100px;
  overflow: hidden;
}

.main-content {
  display: flex;
  align-self: center;
  justify-content: center;
}
